<template>
	<view class="container">
		<view class="register">
			<view class="info_box">
				<view class="info_item">
					<text class="label">客户</text>
					<text>{{roomDet.client}}</text>
				</view>
				<view class="info_item">
					<text class="label">房间</text>
					<text>{{roomDet.roomCode}}</text>
				</view>
				<view class="info_item">
					<text class="label">面积</text>
					<text>{{roomDet.prebuildArea}}m²</text>
				</view>
				<view class="info_item">
					<text class="label">单价</text>
					<text>{{roomDet.buildMonoPrice}}元/m²</text>
				</view>
				<view class="info_item">
					<text class="label">总价</text>
					<text>{{roomDet.standardTotalPrice}}元</text>
				</view>
				<template v-if="roomDet.payType!=2">
					<view class="info_item">
						<text class="label">首付款金额</text>
						<text>{{roomDet.downPaymentAmount}}元</text>
					</view>
					<!-- 商贷、公积金贷 -->
					<view class="info_item" v-if="roomDet.payType!=5">
						<text class="label">贷款金额</text>
						<text>{{roomDet.loanAmount}}元</text>
					</view>
					<!-- 组合贷 -->
					<template v-if="roomDet.payType==5">
						<view class="info_item">
							<text class="label">公积金贷款金额</text>
							<text>{{roomDet.fundLoanAmount}}元</text>
						</view>
						<view class="info_item">
							<text class="label">商业贷款金额</text>
							<text>{{roomDet.commercialLoanAmount}}元</text>
						</view>
					</template>
					<view class="info_item">
						<text class="label">贷款年限</text>
						<text>{{roomDet.loanYearLimit}}年</text>
					</view>
					<view class="info_item">
						<text class="label">还款月数</text>
						<text>{{roomDet.months}}月</text>
					</view>
				</template>
			</view>
			<!-- 下面利息 -->
			<view class="info_box" v-if="roomDet.payType!=2">
				<view class="form_plan">
					<view class="title">等额本息</view>
					<view class="info_item">
						<text class="label">每月还款金额</text>
						<text>{{roomDet.preLoan}}元</text>
					</view>
					<view class="info_item">
						<text class="label">还款总额</text>
						<text>{{roomDet.totalMoney}}元</text>
					</view>
					<view class="info_item">
						<text class="label">还款总利息</text>
						<text>{{roomDet.interest}}元</text>
					</view>
				</view>
				<view class="form_plan">
					<view class="title">等额本金</view>
					<view class="info_item">
						<text class="label">每月还款本金</text>
						<text>{{roomDet.prePrincipal}}元</text>
					</view>
					<view class="info_item">
						<text class="label">首月还款金额</text>
						<text>{{roomDet.firstMonth}}元</text>
					</view>
					<view class="info_item">
						<text class="label">每月利息递减</text>
						<text>{{roomDet.bjInterest}}元</text>
					</view>
					<view class="info_item">
						<text class="label">还款总额</text>
						<text>{{roomDet.bjTotalMoney}}元</text>
					</view>
				</view>
				<view class="form_plan">
					<view class="title">总额差</view>
					<view class="info_item">
						<text class="label">等额本金比等额本息省</text>
						<text>{{totalDifference}}元</text>
					</view>
				</view>
			</view>
		</view>
		<!-- <view class="footer">发送到微信</view> -->
	</view>
</template>

<script>
	export default {
		data() {
			return {
				roomDet: {}, // 置业计划内容
				payMethod: '', // 付款方式
				totalDifference: '' // 总额差
			}
		},
		onReady() {

		},
		onLoad(options) {
			this.payMethod = options.payMethod
			this.roomDet = JSON.parse(decodeURIComponent(options.resData))
			this.totalDifference = (this.roomDet.totalMoney - this.roomDet.bjTotalMoney).toFixed(2)
		},
		methods: {

		}
	}
</script>

<style lang="scss">
	@import '@/styles/form.scss';
	page {
		background-color: #f6f6f6;
	}

	.container {
		.register{
			.info_box{
				.info_item{
					.label{
						min-width: 280rpx;
						width: 280rpx;
					}
				}
				.form_plan {
					border-top: 20rpx solid #f6f6f6;
				
					.title {
						margin-top: 30rpx;
						font-size: 30rpx;
						color: #03958C;
						display: inline-block;
						font-weight: bold;
						position: relative;
						padding-left: 18rpx; 
					}
					
					// .title:before {
					// 	content: ' ';
					// 	border-left: 6rpx solid #03958C;
					// 	display: inline-block;
					// 	height: 25rpx;
					// 	position: absolute;
					// 	top: 50%;
					// 	left: 0;
					// 	margin-top: -10rpx;
					// }
					.saveMoney{
						padding: 30rpx 0;
						text{
							padding-left: 30rpx;
						}
					}
				}
			}
		}  
	}
</style>
